import { mockPrivateSharedContentOwners } from "@rp/react/tanstack-api/file"
import { convertAPIUserToSelectItem } from "./convertAPIUserToSelectItem"

describe("convertAPIUserToSelectItem", () => {
  const selectItems = mockPrivateSharedContentOwners.map(convertAPIUserToSelectItem)

  it("should convert API users to a select items", () => {
    expect(selectItems).toStrictEqual([
      { label: "Carol Carpenter", value: "user-id-0" },
      { label: "Henry Hilton", value: "user-id-1" },
      { label: "Alice Anderson", value: "user-id-2" },
      { label: "Frank Franklin", value: "user-id-3" },
      { label: "Dave Donaldson", value: "user-id-4" },
      { label: "Bob Baker", value: "user-id-5" },
      { label: "Gertrude Gould", value: "user-id-6" },
      { label: "Ellen Ellis", value: "user-id-7" },
    ])
  })
})
